          subtitle: 'Sensing and Visualizing Electric Current Flows of Breadboarded Circuits',
          conference: "UIST '17",
          tech: ['C/C++', 'QT', 'Hardware design'],
          abstract: ['  Electric current and voltage are fundamental to learning, understanding, and debugging circuits. Although both can be measured using tools such as multimeters and oscilloscopes, users have to unplug parts of a circuit and then insert the measuring tools in serial. Furthermore, users need to restore been taken. In practice, this cumber some process poses aformidable barrier to understanding how current flows through-out a circuit. We present CurrentViz, a system that can sense and visualize the electric current flowing through a circuit, which helps users quickly understand otherwise invisible circuit behavior. It supports fully automatic, ubiquitous, and real-time collection of amperage information of breadboarded circuits. It also supports visualization of the amperage data on a circuit schematic to provide an intuitive view into the current state of a circuit.']
